body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.jumbotron {
	background: -webkit-gradient(linear, left top, right top, from(#66ff33), to(yellow));
	background: -webkit-linear-gradient(left, #66ff33, yellow);
	background: -o-linear-gradient(left, #66ff33, yellow);
	background: linear-gradient(to right, #66ff33, yellow);
	border-radius:0px!important;
	margin-top: -20px;
}

.searchForm {
	height:60px;
	font-size:50px;
}

.searchBtn {
	height:60px;
	width:200px;
}

.btn {
	margin-left:5px;
}

.sortBtn {
	margin-right:20px;
}

.active {
	background-color: #66ff33;
	padding: 18px 10px 18px 10px;
}



